html, body {
  margin: 0;
  padding: 0;
  color: #1b1b1b;
  font-family: 'Bowlby One SC', cursive;
  font-size: 16px;
  /* Remove tap side effects on mobile devices (iOS) */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  
}

@media screen and (max-width: 1205px) {
  html, body {
    font-size: 14px;
  }
}

@media screen and (max-width: 1050px) {
  html, body {
    font-size: 13px;
  }
}

@media screen and (max-width: 970px) {
  html, body {
    font-size: 12px;
  }
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 2rem;
  background-color: transparent;
  text-align: center;
}

header h1 {
  position: relative;
  margin: 0;
  color: #222;
  font-weight: normal;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
main {
  width: 75vw;
  margin: 0 auto;
}
.container {
  box-sizing: border-box;
  height: 100vh;
  padding: 5.3rem 0 4rem;
}

.three-root {
  position: relative;
  height: 100%;
  background-color: #fff;
  -webkit-transition: .2s ease-out 0s;
  transition: .2s ease-out 0s;
}

.three-root::before,
.three-root::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 20%;
  height: 40%;
  border-width: 0;
  border-style: solid;
  -webkit-transition: .2s ease-out 0s;
  transition: .2s ease-out 0s;
}

.three-root::before {
  top: -1.1rem;
  left: -1.1rem;
  -webkit-border-image: -webkit-linear-gradient(left, #27dafa, #2580e7) 30 30;
          border-image: linear-gradient(to right, #27dafa, #2580e7) 30 30;
}

.three-root::after {
  right: -1.1rem;
  bottom: -1.1rem;
  -webkit-border-image: -webkit-linear-gradient(right, #27dafa, #2580e7) 30 30;
          border-image: linear-gradient(to left, #27dafa, #2580e7) 30 30;
}

.three-root:hover::before,
.three-root:hover::after {
  border-width: .8rem;
  -webkit-transition: .2s ease-out 1s;
  transition: .2s ease-out 1s;
}

.three-root:hover {
  box-shadow: 0px 0px 0px .5rem #1b1b1b;
  -webkit-transition: .2s ease-out 1.2s;
  transition: .2s ease-out 1.2s;
}

/* Disable canvas selection */
.container canvas {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* Info icon */
.info-icon {
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
  height: 1.2rem;
  width: 1.2rem;
  border: 3px solid #1b1b1b;
  font-size: .5rem;
  border-radius: 50%;
  cursor: pointer;
  text-align: center;
  -webkit-transition: .2s ease 0s;
  transition: .2s ease 0s;
  -webkit-transform: scale(0) rotate(0deg);
          transform: scale(0) rotate(0deg);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.info-icon::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 25%;
  height: 60%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #1b1b1b;
}

.three-root:hover .info-icon {
  -webkit-transform: scale(1) rotate(-360deg);
          transform: scale(1) rotate(-360deg);
  -webkit-transition: -webkit-transform .2s ease 1.5s;
  transition: -webkit-transform .2s ease 1.5s;
  transition: transform .2s ease 1.5s;
  transition: transform .2s ease 1.5s, -webkit-transform .2s ease 1.5s;
}

.info-icon:hover {
  -webkit-animation: rotate 7s linear infinite;
          animation: rotate 7s linear infinite;
}

/* Marquee */
.marquee {
  display: none;
  position: absolute;
  right: 0;
  bottom: 1rem;
  left: 0;
  height: 1.3rem;
  overflow: hidden;
}

.info-icon:hover+.marquee {
  display: block;
}

.marquee-inner {
  position: absolute;
  left: 0;
  width: 200%;
  overflow: hidden;
  -webkit-animation: marquee 10s linear infinite;
          animation: marquee 10s linear infinite;
}

.marquee span {
  float: left;
  width: 50%;
  white-space: pre;
}

/* Font-size cannot be smaller at this width (index.css ->)
 * Continuous effect must be interupted
 * Hide right-line and reset marquee-inner as whole single line 
 */
@media screen and (max-width: 900px) {
  /* Enlarge icon to make tap easier */
  .info-icon {
    height: 1.4rem;
    width: 1.4rem;
  }

  .marquee-inner {
    width: auto;
    -webkit-animation: marquee-single 13s linear infinite;
            animation: marquee-single 13s linear infinite;
  }

  .marquee span {
    width: 100%;
  }

  .right-line {
    display: none;
  }

  /* Touch and show effects  */
  .three-root:hover::before,
  .three-root:hover::after {
    border-width: .8rem;
    -webkit-transition: .2s ease-out;
    transition: .2s ease-out;
  }

  .three-root:hover {
    -webkit-transition: .2s ease-out .2s;
    transition: .2s ease-out .2s;
  }

  .three-root:hover .info-icon {
    -webkit-transition: -webkit-transform .2s ease .2s;
    transition: -webkit-transform .2s ease .2s;
    transition: transform .2s ease .2s;
    transition: transform .2s ease .2s, -webkit-transform .2s ease .2s;
  }
}

@-webkit-keyframes marquee {
  to { left: -100%; }
}

@keyframes marquee {
  to { left: -100%; }
}

@-webkit-keyframes rotate {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
  to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg) }
}

@keyframes rotate {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
  to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg) }
}

@-webkit-keyframes marquee-single {
  from { -webkit-transform: translateX(50%); transform: translateX(50%) }
  to { -webkit-transform: translateX(-100%); transform: translateX(-100%) }
}

@keyframes marquee-single {
  from { -webkit-transform: translateX(50%); transform: translateX(50%) }
  to { -webkit-transform: translateX(-100%); transform: translateX(-100%) }
}
footer {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem 0;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

footer a {
  position: relative;
  color: #1b1b1b;
  text-decoration: none;
  cursor: pointer;
}

footer a::before {
  content: '';
  position: absolute;
  right: -3px;
  bottom: 0;
  left: -3px;
  height: 0;
  z-index: -1;
  -webkit-transition: height .2s ease;
  transition: height .2s ease;
}

footer a:hover::before {
  height: 45%;
  background-image: -webkit-linear-gradient(left, #27dafa, #2580e7);
  background-image: linear-gradient(to right, #27dafa, #2580e7);
}

footer span {
  margin: 3px;
}
